import { useSelector } from "react-redux";
// Chakra Icons
import { BellIcon, SearchIcon } from "@chakra-ui/icons";
// Chakra Imports
import {
	Avatar,
	Button,
	Flex,
	IconButton,
	Input,
	InputGroup,
	InputLeftElement,
	Menu,
	MenuButton,
	MenuItem,
	MenuList,
	Text,
	useColorModeValue,
} from "@chakra-ui/react";
// Assets
import avatar1 from "assets/img/avatars/avatar1.png";
import avatar2 from "assets/img/avatars/avatar2.png";
import avatar3 from "assets/img/avatars/avatar3.png";
// Custom Icons
import { ProfileIcon, SettingsIcon } from "components/Icons/Icons";
// Custom Components
import { ItemContent } from "components/Menu/ItemContent";
import { SidebarResponsive } from "components/Sidebar/Sidebar";
import PropTypes from "prop-types";
import React from "react";
import { NavLink } from "react-router-dom";
import routes from "routes.js";
import SearchBar from "components/Navbars/SearchBar.js";

export default function HeaderLinks(props) {
	const { variant, children, fixed, secondary, onOpen, ...rest } = props;
	const userInfo = useSelector((state) => state.reducerLogin).userInfo;

	// Chakra Color Mode
	let mainTeal = useColorModeValue("teal.300", "teal.300");
	let inputBg = useColorModeValue("white", "gray.800");
	let mainText = useColorModeValue("gray.700", "gray.200");
	let navbarIcon = useColorModeValue("gray.500", "gray.200");
	let searchIcon = useColorModeValue("gray.700", "gray.200");

	if (secondary) {
		navbarIcon = "white";
		mainText = "white";
	}
	const settingsRef = React.useRef();
	return (
		<Flex
			pe={{ sm: "0px", md: "16px" }}
			w={{ sm: "100%", md: "auto" }}
			alignItems="center"
			flexDirection="row"
			justifyContent="flex-end"
		>
			{/* <SearchBar /> */}
			{userInfo === undefined ? (
				<NavLink to="/auth/signin">
					<Button
						ms="0px"
						px="0px"
						me={{ sm: "2px", md: "16px" }}
						color={navbarIcon}
						variant="transparent-with-icon"
						rightIcon={
							document.documentElement.dir ? (
								""
							) : (
								<ProfileIcon
									color={navbarIcon}
									w="22px"
									h="22px"
									me="0px"
								/>
							)
						}
						leftIcon={
							document.documentElement.dir ? (
								<ProfileIcon
									color={navbarIcon}
									w="22px"
									h="22px"
									me="0px"
								/>
							) : (
								""
							)
						}
					>
						<Text display={{ sm: "none", md: "flex" }}>
							Đăng nhập
						</Text>
					</Button>
				</NavLink>
			) : (
				<NavLink to="/remat/profile">
					<Button
						ms="0px"
						px="0px"
						me={{ sm: "2px", md: "16px" }}
						color={navbarIcon}
						variant="transparent-with-icon"
						leftIcon={<Avatar src={userInfo.profilePic} size="sm" />}
					>
						<Text display={{ sm: "none", md: "flex" }}>
							{userInfo.fullname}
						</Text>
					</Button>
				</NavLink>
			)}
			<SidebarResponsive
				logoText={props.logoText}
				secondary={props.secondary}
				routes={routes}
				//logo={logo}
				{...rest}
			/>
			{/* <SettingsIcon
				cursor="pointer"
				ms={{ base: "16px", xl: "0px" }}
				me="16px"
				ref={settingsRef}
				onClick={props.onOpen}
				color={navbarIcon}
				w="18px"
				h="18px"
			/>
			<Menu>
				<MenuButton>
					<BellIcon color={navbarIcon} w="18px" h="18px" />
				</MenuButton>
				<MenuList p="16px 8px">
					<Flex flexDirection="column">
						<MenuItem borderRadius="8px" mb="10px">
							<ItemContent
								time="2 days ago"
								info="Up loaded"
								boldInfo="New song"
								aName="Admin"
								aSrc={avatar1}
							/>
						</MenuItem>
						<MenuItem borderRadius="8px" mb="10px">
							<ItemContent
								time="2 days ago"
								info="Created"
								boldInfo="New Album"
								aName="Admin"
								aSrc={avatar2}
							/>
						</MenuItem>
						<MenuItem borderRadius="8px">
							<ItemContent
								time="3 days ago"
								info="joined your playlist!"
								boldInfo="New friend"
								aName="Admin"
								aSrc={avatar3}
							/>
						</MenuItem>
					</Flex>
				</MenuList>
			</Menu> */}
		</Flex>
	);
}

HeaderLinks.propTypes = {
	variant: PropTypes.string,
	fixed: PropTypes.bool,
	secondary: PropTypes.bool,
	onOpen: PropTypes.func,
};
